footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer>p{
    color: black;
}
footer:hover{
    background: var(--color-btn);
}
footer a{
    color: var(--color-bg);
}

.footer_logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.footer_logo_size{
    width: 2.5rem;
    height: 2.5rem;
    display: inline-block;
    margin: 0 0.5rem -0.5rem 0 ;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer_socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem ;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    font-size: 1.2rem;
}

.footer_socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/*=============MEDIA QUERIES (MEDIAM) =============*/
@media screen and (max-width:1024px){
    
    }
    
    /*=============MEDIA QUERIES (SMALL) =============*/
    @media screen and (max-width:600px){
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer_socials{
        margin-bottom: 2.6rem;
    }
    }
