nav{
    background: rgba(0, 0,0,0.3);
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    margin-left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1.5rem;
    border-radius: 1rem;
    backdrop-filter: blur(15px); 
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 40%;
    display: flex;
    color: var(--color-light);
    font-size: 1.2rem;
}
nav a:hover{
    background: rgba(0,0,0,0.3);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}

/*=============MEDIA QUERIES (SMALL) =============*/
@media screen and (max-width:430px){
    nav a{
        font-size: 1rem;
        padding: 0.5rem;
    }
    
}