header {
  height: 100vh;
  padding-top: 5rem;
  overflow: hidden;
  width: auto;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/*================= CTA ==================*/
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/*================= ME ==================*/

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 25rem;
  height: 35rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 49% 51% 55% 45% / 91% 10% 90% 9%;
  overflow: hidden;
  padding: 3rem 1rem 1.5rem 1.5rem;
}

#me-img {
  width: 25rem;
  height: 35rem;
  overflow: hidden;
  margin-left: 2rem;
  transition: var(--transition02);
}
#me-img:hover {
  margin-top: 0.5rem;
  transition: var(--transition02);
}

span:nth-child(1) {
  color: var(--color-btn);
}

/*=============MEDIA QUERIES (MEDIAM) =============*/
@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/*=============MEDIA QUERIES (SMALL) =============*/
@media screen and (max-width: 600px) {
  header {
    left: 50rem;
    height: 110vh;
  }
  .header_socials,
  .scroll_down {
    display: none;
  }
  .me {
    left: calc(50% - 12.5rem);
  }
}
